/* stylelint-disable color-function-notation */
/* stylelint-disable alpha-value-notation */
:root {
  --font-family: "Roboto", sans-serif;
  --content-width: 1160px;
  --content-fluid-width: 1790px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --accent-color: #d52027;
  --dark-color: #141024;
  --text-color: rgba(20, 16, 36, 0.7);
  --main-bg-color: var(--light-color);
  --main-scrollbar-width: 6px;
  --main-scrollbar-color: var(--light-color);
  --main-scrollbar-thumb-color: var(--accent-color);
}

