*,
*::before,
*::after {
  box-sizing: inherit;
  border-radius: 0;
}

html {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  min-height: 100vh;
  -webkit-text-size-adjust: none;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  background-color: var(--main-bg-color);
}
body.body-locked {
  overflow: hidden;
}

footer {
  margin-top: auto;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.section-mb {
  margin-bottom: 20px;
}

.section-padding {
  padding-top: 100px;
}
@media (max-width: 1440px) {
  .section-padding {
    padding-top: 60px;
  }
}
@media (max-width: 1024px) {
  .section-padding {
    padding-top: 20px;
  }
}

.fieldset-default {
  margin: 0;
  padding: 0;
  border: 0;
}

.text-nowrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.img-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.img-contain {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.button-default {
  border: 0;
  margin: 0;
  padding: 0;
  background-color: transparent;
}

.flex-centered-content, .main-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-wrap {
  display: flex;
  flex-wrap: wrap;
}

.main-scroll::-webkit-scrollbar, ::-webkit-scrollbar {
  background-color: var(--main-scrollbar-color);
  width: var(--main-scrollbar-width);
}
.main-scroll::-webkit-scrollbar-thumb, ::-webkit-scrollbar-thumb {
  background-color: var(--main-scrollbar-thumb-color);
}

.text-overflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.overflow-hidden {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #ffffff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-fluid {
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-fluid-width);
  outline: 1px solid black;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.hover-opacity, .main-btn {
  transition: opacity var(--main-transition);
}
.hover-opacity:hover, .main-btn:hover, .hover-opacity:focus, .main-btn:focus {
  opacity: 0.7;
}

.main-btn {
  font-family: "Open-Sans";
  width: 100%;
  max-width: fit-content;
  min-height: 55px;
  padding: 15px;
  color: var(--white-color);
  background-color: var(--gray-color);
  border: 1px solid transparent;
}
@media (max-width: 600px) {
  .main-btn {
    min-height: 40px;
    padding: 10px;
  }
}

.background-color-hover {
  transition: background-color var(--main-transition);
}
.background-color-hover:hover, .background-color-hover:focus {
  opacity: 1;
  background-color: var(--hover-black);
}

.background-white-hover {
  transition: background-color var(--main-transition), color var(--main-transition);
}
.background-white-hover:hover, .background-white-hover:focus {
  opacity: 1;
  color: var(--gray-color);
  background-color: var(--white-color);
}

/* Компоненты */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-overlay.is-opened {
  display: flex;
}

.modal {
  position: relative;
  width: 90vw;
  max-width: 600px;
  height: 90vh;
  max-height: 600px;
  background-color: #ffffff;
}
.modal__close {
  position: absolute;
  top: 0;
  right: 0;
}
.modal__content {
  overflow: hidden;
  overflow-y: auto;
  height: 100%;
}
.modal.is-active {
  animation: showModal 0.4s linear forwards;
}
@keyframes showModal {
  0% {
    transform: translateX(-105vw);
  }
  100% {
    transform: translateX(0);
  }
}

/* Секции */
.anim1 body {
  background-color: #e6e6e6 !important;
}
.anim1 .intro {
  outline: 1px solid red;
}
.anim1 .intro span {
  position: relative;
  width: 36px;
  font-weight: 500;
  line-height: 1.2;
  color: #101314;
  text-align: center;
  text-transform: uppercase;
}
.anim1 .intro__text {
  position: relative;
  outline: 1px solid red;
  width: fit-content;
  margin: 0 auto;
  padding: 0 50px;
  opacity: 0;
}
.anim1 .intro__text::before, .anim1 .intro__text::after {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  content: "";
  border-radius: 50%;
  background-color: #101314;
}
.anim1 .intro__text::before {
  left: 30px;
}
.anim1 .intro__text::after {
  right: 30px;
}
.anim1 .intro__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  outline: 1px solid black;
  padding: 20px;
  overflow: hidden;
}
.anim1 .intro__logo svg {
  width: 80vw;
  height: fit-content;
  max-width: 1000px;
  max-height: 700px;
}
.anim1 .intro__logo svg path[data-id=big-letter] {
  opacity: 0;
  transform: translateY(150px);
}
.anim1 .intro__logo svg path[data-id=small-letter] {
  opacity: 0;
  transform: translateX(500px);
}
.anim1 .intro__logo svg g[data-id=home-icon] {
  opacity: 0;
  transform: translateY(350px);
}
body {
  background-color: #EDF0F5;
}

.anim2 .intro__scroll {
  position: relative;
  border: 1px solid #101314;
  margin: 0 auto;
  margin-top: 50px;
  opacity: 0;
  transform: translateY(300px);
  border-radius: 40px;
  width: 40px;
  height: 80px;
}
.anim2 .intro__scroll::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 2px;
  height: 20px;
  content: "";
  background-color: #101314;
  animation: scroll 0.5s 0.2s ease-in infinite;
}
@keyframes animation {
  0% {
    transform: translate(0%, -50%);
  }
  100% {
    transform: translate(0%, -50%);
  }
}
.anim2 .intro span {
  position: relative;
  font-size: 36px;
  font-weight: 500;
  line-height: 1.2;
  color: #101314;
  text-align: center;
  text-transform: uppercase;
  color: white;
  text-shadow: 0 1px 2px black;
}
.anim2 .intro__text {
  position: relative;
  width: fit-content;
